<template>
  <tr>
        <td>
          <button type="button" :class="['btn', job.state.active ? 'btn-success' : 'btn-secondary']"
            :disabled="!job.locks.allOpen" :title="job.locks.lockStrings()"
            v-on:click="setActiveJob(job)" style="width: 100%">{{ job.locks.allOpen ? job.name : '?' }}</button>
        </td>
        <td>
          {{ job.xp.state.level }}
        </td>
        <td>
          <progress class="progress" :value="job.xp.state.xp" :max="job.xp.nextLevelAtXp(job.xp.state.level)"></progress>
        </td>
        <td>
          {{ job.xp.state.xp.toFixed(0) }}xp / {{ job.xp.nextLevelAtXp(job.xp.state.level) }}xp<br>
        </td>
        <td>
          {{ job.locks.allOpen ? job.income.effective.toFixed(0) + '$/h' : '?' }}
        </td>
    </tr>
</div>

</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'job-button',
  props: [
    'job'
  ],
  computed: mapGetters([]),
  methods: mapActions(['setActiveJob'])
}
</script>



