import { Stat } from './stat'
import { JobLevelModifier } from '../modifiers/jobLevel'

export class IncomePerWorkHourStat extends Stat {
  constructor (job, basePay) {
    super(job.id + 'incpH', 'Income/h', job.basePay)
    this.job = job
    this.base = basePay
    this.addModifier(new JobLevelModifier(job))
    this.update()
  }
}



