<template>
  <div>
    <div v-for="area in field.areas">
      <div class="row">
        <h5>{{ area.name }}</h5><br>
      </div>
      <table>
        <tr>
          <th style='width:20rem'>Title</th><th style='width:10rem'>Hours</th>
        </tr>
        <subject-button v-for="subject in area.subjects" :subject="subject" v-if="subject.unlocked()"></subject-button>
      </table>
    </div>
  </div>
</template>
<script>
import SubjectButton from './SubjectButton'

export default {
  name: 'field-display',
  components: {
    SubjectButton
  },
  props: ['field']
}
</script>



