import { createCompletedOrReadingListLock, createCompletedLock } from '../../locks'
import { Book } from '../book'
import { Area } from '../area'
import { Modifier, LevelModifier } from '../../modifiers/modifier'
import { slacking } from '../../schedule/activity'
import { workExperiencePerHourStat } from '../../stats/workExperiencePerHour'
import { energyStat } from '../../stats/energy'

class EatThatFrog extends Book {
  constructor () {
    super('gsd1', 'Eat That Frog!', 'Stop Procrastinating', 20,
    'https://www.amazon.com/Eat-That-Frog-Great-Procrastinating/dp/1576754227/ref=zg_bs_9473326011_2?_encoding=UTF8&psc=1&refRID=JAF5GHHM20Y91DPP9TFK')
    const mod = new Modifier('gsd1_slacking_mod', 'Eat That Frog!', 1, 0.8)
    createCompletedLock(this, mod)
    slacking.duration.addModifier(mod)
    this.effect = '20% less slacking'
  }
}

let eatThatFrog = new EatThatFrog()

class TheOneThing extends Book {
  constructor () {
    super('gsd2', 'The One Thing', 'Prioritize Harder', 30,
    'https://www.amazon.com/ONE-Thing-Surprisingly-Extraordinary-Results/dp/1885167776/ref=sr_1_1?s=books&ie=UTF8&qid=1484905467&sr=1-1&keywords=the+one+thing')
    const mod = new Modifier('gsd2_job_experience_mod', 'The One Thing', 1, 1.1)
    createCompletedLock(this, mod)
    workExperiencePerHourStat.addModifier(mod)
    this.effect = '10% more work experience/h'
  }
}

let theOneThing = new TheOneThing()

class GettingThingsDone extends Book {
  constructor () {
    super('gsd3', 'Getting Things Done', 'Organize Your Life', 60,
    'https://www.amazon.com/Getting-Things-Done-Stress-Free-Productivity/dp/0143126563')
    const mod = new Modifier('gsd3_job_experience_mod', 'Getting Things Done', 1, 1.1)
    createCompletedLock(this, mod)
    energyStat.addModifier(mod)
    this.effect = '10% more energy'
  }
}

let gettingThingsDone = new GettingThingsDone()

createCompletedOrReadingListLock(eatThatFrog, theOneThing)
createCompletedOrReadingListLock(theOneThing, gettingThingsDone)

class GetStuffDone extends Area {
  constructor () {
    super('area_getting_stuff_done', 'Getting Stuff Done', [eatThatFrog, theOneThing, gettingThingsDone])
    const mod = new LevelModifier('gsd_research_level_mod', 'Research: Getting Things Done', 2, this.xp, 1.01)
    energyStat.addModifier(mod)
    this.effect = '1% more energy per level'
  }
}

export const getStuffDone = new GetStuffDone()



