<template>
<div>
  <div class="row">
        Age {{ (18 + Math.floor(time.currentDay.getValue() / 365)).toFixed(0) }}
        Day {{ time.currentDay.getValue() % 365 }}
  </div>
  <!--
  <div class="row">
        <svg id="clock" width="75%" viewBox="0 0 100 100">

  	<circle id="face" cx="50" cy="50" r="25" stroke-width="13" transform="rotate(270, 50, 50)"
    :stroke-dasharray="dashArray(scheduleItem)" :stroke="scheduleItem[1].color" v-for="scheduleItem in reverseSchedule"/>
  	<g id="hands">
  		<rect id="hour" x="48.5" y="12.5" width="1" height="40" rx="2.5" ry="2.55" :transform="clockRotation" />
  	</g>
  </svg>
  </div>
  <div class="row">
        {{ currentActivity.ingName }}
  </div>
-->
</div>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  name: 'clock-display',
  computed: { clockRotation: function () {
    return 'rotate(' + this.time.currentTime * 15 + ', 50, 50)'
  },
    ...mapGetters(['time', 'reverseSchedule', 'currentActivity'])
  },
  methods: {
    dashArray: function (scheduleItem) {
      return scheduleItem[0][1] * 6.55 + ' 10000' // 6.55 depends on the radius of the circle (2piR/24)
    }
  }
}
</script>

<style>
#face {
}
#hour {
	stroke-width: 1px; fill: #533; stroke: #222;
}
#sec { stroke: #f55; }
</style>



