import { createCompletedOrReadingListLock, createCompletedLock } from '../../locks'
import { Book } from '../book'
import { Area } from '../area'
import { Modifier, LevelModifier } from '../../modifiers/modifier'
import { workExperiencePerHourStat } from '../../stats/workExperiencePerHour'

class Leadership101 extends Book {
  constructor () {
    super('leadership1', 'Leadership 101', 'Fundamentals of Leadering', 50,
    'https://www.amazon.com/Leadership-101-Every-Leader-Needs/dp/0785264191/ref=pd_sim_14_8?_encoding=UTF8&psc=1&refRID=DGE2PYMMTAJT5FG39Q1D')
    const mod = new Modifier(this.id + '_workexp_mod', this.name, 1, 1.05)
    createCompletedLock(this, mod)
    workExperiencePerHourStat.addModifier(mod)
    this.effect = '5% more work experience/hour'
  }
}

export const leadership101 = new Leadership101()

class GungHo extends Book {
  constructor () {
    super('leadership2', 'Gung Ho', 'Inspire Underlings', 70,
    'https://www.amazon.com/Gung-Turn-People-Any-Organization/dp/068815428X/ref=pd_sbs_14_4?_encoding=UTF8&psc=1&refRID=HSD5108AR57SAAKEDTM3')
    const mod = new Modifier(this.id + '_workexp_mod', this.name, 1, 1.05)
    createCompletedLock(this, mod)
    workExperiencePerHourStat.addModifier(mod)
    this.effect = '5% more work experience/hour'
  }
}

export const gungHo = new GungHo()

class OneMinuteManager extends Book {
  constructor () {
    super('leadership3', 'The One Minute Manager', 'Sequel to \'The One Minute Lover\'', 50,
    'https://www.amazon.com/New-One-Minute-Manager/dp/0062367544/ref=pd_sim_14_5?_encoding=UTF8&psc=1&refRID=PEBQCX8KNMN5SE6RNZNY')
    const mod = new Modifier(this.id + '_workexp_mod', this.name, 1, 1.05)
    createCompletedLock(this, mod)
    workExperiencePerHourStat.addModifier(mod)
    this.effect = '5% more work experience/hour'
  }
}

const oneMinuteManager = new OneMinuteManager()

class TheLeadershipChallenge extends Book {
  constructor () {
    super('leadership4', 'The Leadership Challenge', 'Extraordinize your Organization', 90,
    'https://www.amazon.com/Leadership-Challenge-Extraordinary-Things-Organizations/dp/0470651725/ref=pd_sim_14_3?_encoding=UTF8&psc=1&refRID=FHPWWX6JRBW0QHPDR2BX')
    const mod = new Modifier(this.id + '_workexp_mod', this.name, 1, 1.05)
    createCompletedLock(this, mod)
    workExperiencePerHourStat.addModifier(mod)
    this.effect = '5% more work experience/hour'
  }
}

const theLeadershipChallenge = new TheLeadershipChallenge()

class LeadershipTheoryAndPractice extends Book {
  constructor () {
    super('leadership5', 'Leadership: Theory and Practice', 'Getting Serious', 150,
    'https://www.amazon.com/gp/product/1483317536/ref=s9_acsd_top_hd_bw_bhG_c_x_7_w?pf_rd_m=ATVPDKIKX0DER&pf_rd_s=merchandised-search-3&pf_rd_r=0T6BTTVQGTHZ79B4BYK5&pf_rd_r=0T6BTTVQGTHZ79B4BYK5&pf_rd_t=101&pf_rd_p=f5270322-e81a-47de-969b-e67cbfc5917d&pf_rd_p=f5270322-e81a-47de-969b-e67cbfc5917d&pf_rd_i=2682')
    const mod = new Modifier(this.id + '_workexp_mod', this.name, 1, 1.05)
    createCompletedOrReadingListLock(this, mod)
    workExperiencePerHourStat.addModifier(mod)
    this.effect = '5% more work experience/hour'
  }
}

const leadershipTheoryAndPractice = new LeadershipTheoryAndPractice()

createCompletedOrReadingListLock(leadership101, gungHo)
createCompletedOrReadingListLock(gungHo, oneMinuteManager)
createCompletedOrReadingListLock(oneMinuteManager, theLeadershipChallenge)
createCompletedOrReadingListLock(theLeadershipChallenge, leadershipTheoryAndPractice)

class Leadership extends Area {
  constructor () {
    super('area_leadership', 'Leadership', [leadership101, gungHo, oneMinuteManager, theLeadershipChallenge, leadershipTheoryAndPractice])
    const mod = new LevelModifier('leadership_research_level_mod', 'Research: Leadership', 2, this.xp, 1.01)
    workExperiencePerHourStat.addModifier(mod)
    this.effect = '1% more work experience/hour per level'
  }
}

export const leadership = new Leadership()



