import { Stat } from './stat'
import { dailyIncomeStat } from './dailyIncome'
import { dailyExpensesStat } from './dailyExpenses'

class DailyNetIncomeStat extends Stat {
  constructor () {
    super('dailyNetIncomeStat', 'Net Income/day', 0)
    dailyIncomeStat.dependants.push(this)
    dailyExpensesStat.dependants.push(this)
    this.update()
  }

  update () {
    this.base = dailyIncomeStat.effective - dailyExpensesStat.effective
    super.update()
  }
}

export const dailyNetIncomeStat = new DailyNetIncomeStat()



