import { Stat } from './stat'
import { rentPerDayStat } from './rentPerDay'
import { foodCostsPerDayStat } from './foodCostsPerDay'

class DailyExpensesStat extends Stat {
  constructor () {
    super('dailyExpensesStat', 'Expenses/day', 0)
    rentPerDayStat.dependants.push(this)
    foodCostsPerDayStat.dependants.push(this)
    this.update()
  }

  update () {
    this.base = rentPerDayStat.effective + foodCostsPerDayStat.effective
    super.update()
  }
}

export const dailyExpensesStat = new DailyExpensesStat()



