import { BasicEntity } from '../basicEntity'

class CareerState {}

export class Career extends BasicEntity {
  constructor (id, name, jobs) {
    super(id, name, new CareerState())
    this.jobs = jobs
    for (let job of jobs) {
      job.dependants.push(this)
    }
  }

  prestige () {
  }

  update () {
    super.update()
  }
}



