import { Locks } from './locks'
import { saver } from './save'
import { assert } from './assert'

export class BasicEntity {
  constructor (id, name, state) {
    this.id = id
    this.name = name
    this.locks = new Locks()
    this.state = state
    this.dependants = []
    saver.register(this)
  }

  onLoad () {}

  subscribe (dependant) {
    this.dependants.push(dependant)
  }

  unsubscribe (dependant) {
    this.dependants = this.dependants.filter(obj => obj.id !== dependant.id)
  }

  update () {
    this.locks.update()
    for (let dependant of this.dependants) {
      dependant.update()
    }
  }

  prestige () {}

  postPrestigeAssert () {
    for (let lock of this.locks.locks) {
      assert(!lock.open)
    }
    assert(this.locks.locks.length <= 0 || !this.locks.allOpen)
  }

  unlocked () {
    return this.locks.allOpen
  }
}



