import { BasicEntity } from '../basicEntity'

class CurrentResearchContainer extends BasicEntity {
  constructor () {
    super('currentResearchContainer', 'Current Research Container', {})
    this.area = null
  }

  prestige () {
    this.area = null
  }

  setCurrentResearch (area) {
    if (area === this.area) {
      return
    }
    if (this.area) {
      this.area.deactivate()
    }
    this.area = area
    this.area.activate()
    super.update()
  }
}

export const currentResearchContainer = new CurrentResearchContainer()



