export class Lock {
  constructor (description, check, keyholders) {
    this.description = description
    this._check = check
    this.keyholders = keyholders
    this.open = false
  }

  keyholdersUnlocked () {
    let keyholdersUnlocked = true
    for (let keyholder of this.keyholders) {
      keyholdersUnlocked = keyholdersUnlocked && keyholder.locks.allOpen
    }
    return keyholdersUnlocked
  }

  update () {
    this.open = this._check()
  }
}

export class Locks {
  constructor () {
    this.locks = []
    this.allOpen = true
  }

  addLock (lock) {
    this.locks.push(lock)
    this.update()
  }

  update () {
    this.allOpen = true
    for (let lock of this.locks) {
      lock.update()
      this.allOpen = this.allOpen && lock.open
    }
  }

  keyholdersUnlocked () {
    for (let lock of this.locks) {
      if (!lock.keyholdersUnlocked()) {
        return false
      }
    }
    return true
  }

  lockStrings () {
    let descriptions = []
    for (let lock of this.locks) {
      if (!lock.open) {
        descriptions.push(lock.description)
      }
    }
    return descriptions
  }
}

export const createAnyLock = function (requiredEntities, lockedEntity, predicate) {
  const lockCheck = function () {
    let anyCompleted = false
    for (let entity of requiredEntities) {
      anyCompleted = anyCompleted || predicate(entity)
    }
    return anyCompleted
  }
  let idString = 'Any Completed:'
  for (let entity of requiredEntities) {
    entity.subscribe(lockedEntity)
    idString += ' ' + entity.name
  }
  lockedEntity.locks.addLock(new Lock(idString, lockCheck, requiredEntities))
}

export const createMinimumValueLock = function (requiredEntity, lockedEntity, minimumValue) {
  const lockCheck = function () {
    return requiredEntity.getValue() >= minimumValue
  }
  lockedEntity.locks.addLock(new Lock(requiredEntity.name + ' at least ' + minimumValue, lockCheck, [requiredEntity]))
  requiredEntity.subscribe(lockedEntity)
}

export const createSelectedLock = function (requiredEntity, lockedEntity) {
  const lockCheck = function () {
    return requiredEntity.state.selected
  }
  lockedEntity.locks.addLock(new Lock(requiredEntity.name + ' selected', lockCheck, [requiredEntity]))
  requiredEntity.subscribe(lockedEntity)
}

export const createLevelLock = function (requiredEntity, lockedEntity, requiredLevel) {
  const lockCheck = function () {
    return requiredEntity.xp.state.level >= requiredLevel
  }
  lockedEntity.locks.addLock(new Lock(requiredEntity.name + ' Level ' + requiredLevel, lockCheck, [requiredEntity]))
  requiredEntity.xp.subscribe(lockedEntity)
}

export const createLevelLockChain = function (entities, level) {
  for (let i = 1; i < entities.length; i++) {
    createLevelLock(entities[i - 1], entities[i], level)
  }
}

export const createCompletedLock = function (requiredEntity, lockedEntity) {
  const lockCheck = function () {
    return requiredEntity.state.completed
  }
  lockedEntity.locks.addLock(new Lock(requiredEntity.name, lockCheck, [requiredEntity]))
  requiredEntity.subscribe(lockedEntity)
}

export const createCompletedOrReadingListLock = function (requiredEntity, lockedEntity) {
  const lockCheck = function () {
    return requiredEntity.state.completed || requiredEntity.state.onReadingList
  }
  lockedEntity.locks.addLock(new Lock(requiredEntity.name, lockCheck, [requiredEntity]))
  requiredEntity.subscribe(lockedEntity)
}



