<template>
  <div class="modal" id="progress-modal" data-backdrop='static'>
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Idle Progress</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <p>Calculating progress since you left.</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'progress-modal'
}
</script>



