import { BasicEntity } from '../basicEntity'
import { activities, sleep, freeTime } from './activity'
import { assert } from '../assert'

class ScheduleState {
}

class Schedule extends BasicEntity {
  constructor (id, name, activities) {
    super(id, name, new ScheduleState())
    for (let activity of activities) {
      activity.duration.dependants.push(this)
    }
    this.timetable = [] // not in the state, because the timetable can be reconstructed from activity state
    this.update()
  }

  prestige () {
  }

  postPrestigeAssert () {
    assert(this.totalTime() === 24 * 60)
  }

  totalTime () {
    return activities.map(a => a.getDuration()).reduce((x, y) => x + y)
  }

  update () {
    let totalTime = this.totalTime()
    if (totalTime !== 24 * 60) {
      freeTime.durationProxy.add(24 * 60 - totalTime)
      return // update is called again when free time duration has been updated - so we can safely return here
    }
    assert(totalTime === 24 * 60)
    let time = 0
    let timetable = []
    for (let activity of activities) {
      timetable.push([[time, time + activity.getDuration()], activity])
      time += activity.getDuration()
    }
    this.timetable = timetable
    super.update()
  }

  lookupCurrentActivity (time, returnTimeLeft) {
    for (let [interval, activity] of this.timetable) {
      if (interval[0] <= time && time < interval[1]) {
        if (returnTimeLeft) {
          return [activity, interval[1] - time]
        } else {
          return activity
        }
      }
    }
    console.log('didn\' find current activity', time, this.timetable)
    if (returnTimeLeft) {
      return [sleep, 24 * 60 - time]
    } else {
      return sleep
    }
  }
}

export const schedule = new Schedule('schedule', 'Schedule', activities)



