import { Stat } from './stat'
import { currentIncomeContainer } from '../jobs/currentJobContainer'
import { work } from '../schedule/activity'

class DailyIncomeStat extends Stat {
  constructor () {
    super('dailyIncomeStat', 'Income/day', 0)
    currentIncomeContainer.dependants.push(this)
    work.dependants.push(this)
    this.update()
  }

  update () {
    if (currentIncomeContainer.hasOwnProperty('income')) {
      this.base = currentIncomeContainer.income.effective * work.duration.effective / 60
    }
    super.update()
  }
}

export const dailyIncomeStat = new DailyIncomeStat()



