import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import { bunMasters } from './jobs/bunMasters'
import { yugle } from './jobs/yugle'
import { currentJobContainer } from './jobs/currentJobContainer'
import { areas, fields } from './library/fields'
import { energyStat } from './stats/energy'
import { workExperiencePerHourStat } from './stats/workExperiencePerHour'
import { dailyExpensesStat } from './stats/dailyExpenses'
import { dailyIncomeStat } from './stats/dailyIncome'
import { dailyNetIncomeStat } from './stats/dailyNetIncome'
import { schedule } from './schedule/schedule'
import { activities, research } from './schedule/activity'
import { currentDay } from './gameState/currentDay'
import { autoPromote } from './gameState/autoPromote'
import { money } from './gameState/currencies'
import { readingList } from './library/readingList'
import { currentResearchContainer } from './library/currentResearchContainer'
import { createAnyLock } from './locks'
import { events } from './events/event'
import { saver } from './save'
import { prestiger } from './prestige'
import { assert } from './assert'

const careers = [bunMasters, yugle]

createAnyLock(areas, research, area => area.unlocked())

const timeConfig = {
  ticksPerDay: 1,
  tickLength: 20,
  sessionStart: 0,
  sessionTicks: 0
}

const currency = {
  money: money
}

const time = {
  currentDay: currentDay,
  currentTick: 0,
  currentTime: 0,
  lastTime: 0
}

const stats = {
  energy: energyStat,
  workExperiencePerHour: workExperiencePerHourStat,
  dailyExpenses: dailyExpensesStat,
  dailyIncome: dailyIncomeStat,
  dailyNetIncome: dailyNetIncomeStat
}

const state = {
  currency: currency,
  readingList: readingList,
  autoPromote: autoPromote,
  time: time,
  stats: stats,
  careers: careers,
  schedule: schedule,
  activities: activities,
  timeConfig: timeConfig,
  fields: fields,
  research: research,
  events: events
}

const getters = {
  currency: state => state.currency,
  time: state => state.time,
  stats: state => state.stats,
  timeConfig: state => state.timeConfig,
  reverseSchedule: state => state.schedule.timetable.slice().reverse(),
  schedule: state => state.schedule,
  activities: state => state.activities,
  careers: state => state.careers,
  fields: state => state.fields,
  jobs: state => state.jobs,
  autoPromote: state => state.autoPromote,
  readingList: state => state.readingList,
  research: state => state.research,
  events: state => state.events
}

let advanceSchedule = function (state) {
  state.time.currentTick += 1
  if (state.time.currentDay.getValue() % 10 === 0) {
    saver.save()
  }
  state.currency.money.add(state.stats.dailyNetIncome.effective)
  state.time.currentTick = 0
  state.time.currentDay.incrementValue()
  for (let activity of state.activities) {
    activity.do(state)
  }
  state.time.lastTime += state.timeConfig.tickLength
}

let tickTime = function (state, ticks) {
  while (ticks > 0) {
    ticks -= 1
    state.time.sessionTicks += 1
    advanceSchedule(state)
  }
  if (state.time.currentDay.getValue() > 365 * 10) {
    console.log('prestiging')
    prestiger.prestige()
  }
}

const findActiveJob = function (state) {
  for (let career of state.careers) {
    for (let job of career.jobs) {
      if (job.state.active) {
        return job
      }
    }
  }
}

// define the possible mutations that can be applied to our state
const mutations = {
  TICK (state, ticks) {
    tickTime(state, ticks)
  },
  INITIALIZE_TIMING (state) {
    let dateObject = new Date()
    state.time.lastTime = dateObject.getTime()
    state.time.sessionStart = state.time.lastTime
    currentJobContainer.setCurrentJob(findActiveJob(state))
    saver.load()
    currentJobContainer.setCurrentJob(findActiveJob(state))
  },
  CHANGE_ACTIVITY_TIME (state, activityDelta) {
    let activity = activityDelta[0]
    let delta = activityDelta[1]
    console.log(delta)
    let absDelta = Math.abs(delta)
    let deltaRoundingLower = activity.duration.effective - absDelta * Math.floor(activity.duration.effective / absDelta)
    let deltaRoundingUpper = absDelta * Math.ceil(activity.duration.effective / absDelta) - activity.duration.effective
    if (deltaRoundingLower > 0) {
      delta = Math.max(delta, -deltaRoundingLower)
    }
    if (deltaRoundingUpper > 0) {
      delta = Math.min(delta, deltaRoundingUpper)
    }
    console.log(delta)
    let freeTime = state.activities[state.activities.length - 1]
    delta = Math.min(delta, freeTime.duration.effective) // upper limit: available free time
    delta = Math.max(delta, -activity.duration.effective) // lower limit: remaining activity duration
    console.log(delta)
    activity.durationProxy.add(delta)
    console.log(activity.durationProxy.getValue())
    console.log(activity.duration.base)
    freeTime.durationProxy.add(-delta)
    assert(activity.duration.effective === activity.durationProxy.getValue())
    state.schedule.update()
  },
  SET_ACTIVE_JOB (state, job) {
    currentJobContainer.setCurrentJob(job)
  },
  SET_ACTIVE_RESEARCH (state, area) {
    currentResearchContainer.setCurrentResearch(area)
  },
  ADD_TO_READING_LIST (state, subject) {
    if (subject.locks.allOpen && !subject.state.isOnReadingList && !subject.state.completed) {
      subject.addToReadingList()
    }
  },
  HARD_RESET (state) {
    saver.stop()
    window.localStorage.clear()
    window.location.reload()
  }
}

const actions = {
  tick: ({ commit }, ticks) => {
    commit('TICK', ticks)
  },
  initializeTiming: ({ commit }) => {
    commit('INITIALIZE_TIMING')
  },
  changeActivityTime: ({ commit }, activityDelta) => {
    commit('CHANGE_ACTIVITY_TIME', activityDelta)
  },
  setActiveJob: ({ commit }, job) => {
    commit('SET_ACTIVE_JOB', job)
  },
  setActiveResearch: ({ commit }, area) => {
    commit('SET_ACTIVE_RESEARCH', area)
  },
  addToReadingList: ({ commit }, subject) => {
    commit('ADD_TO_READING_LIST', subject)
  },
  hardReset: ({ commit }) => {
    commit('HARD_RESET')
  }
}

// create the Vuex instance by combining the state and mutations objects
// then export the Vuex store for use by our components
export default new Vuex.Store({
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions
})



