import { BasicEntity } from '../basicEntity'

class StatState {
}

export class Stat extends BasicEntity {
  constructor (id, name, base) {
    super(id, name, new StatState())
    this.id = id
    this.name = name
    this.base = base
    this.modifiers = []
    this.update()
  }

  prestige () {
  }

  update () {
    this.calculateEffective()
    super.update()
  }

  setBase (base) {
    this.base = base
    this.update()
  }

  addModifier (modifier) {
    modifier.subscribe(this)
    this.modifiers.push(modifier)
    this.modifiers.sort((a, b) => a.priority - b.priority)
  }

  calculateEffective () {
    this.effective = this.base
    for (let modifier of this.modifiers.filter(m => m.unlocked())) {
      this.effective = modifier.apply(this.effective)
    }
  }
}



