<template>
  <div>
<h5>Reading List:</h5>
<div v-for="book in readingList.books"><small>{{ book.name }}<br>{{ book.state.timeLeft }}h left</small></div>
  </div>
</template>
<script>
import {
  mapGetters
} from 'vuex'

export default {
  name: 'reading-list',
  computed: mapGetters(['readingList'])
}
</script>



