<template>
<table>
      <tr v-for="activity in activities" :style="{ backgroundColor: activity.isCurrent ? '#DDD' : '#FFF' }" :class="[activity.unlocked() ? 'item-unlocked' : 'item-locked']">
          <td style='width:5rem'>{{ activity.name }}</td>
          <td style='width:3rem'>{{ activity.duration.durationString }}</td>
          <td style='width:1rem'><button v-on:click="changeActivityTime([activity, 60])" v-if="activity.durationByUser">+</button></td>
          <td style='width:1rem'><button v-on:click="changeActivityTime([activity, -60])" v-if="activity.durationByUser">-</button></td>
      </tr>
</table>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'schedule-controller',
  computed: mapGetters(['activities', 'research']),
  methods: mapActions(['changeActivityTime'])
}
</script>



