import { BasicEntity } from '../basicEntity'
import { prestiger } from '../prestige'
import { createMinimumValueLock } from '../locks'
import { currentDay } from '../gameState/currentDay'

class NodeState {
  constructor () {
    this.executed = false
  }
}

class PathState {
  constructor () {
    this.selected = false
    this.enabled = true
  }
}

class Path extends BasicEntity {
  constructor (id, name, nextNode) {
    super(id, name, new PathState())
    this.nextNode = nextNode
  }

  prestige () {
    this.state.selected = false
    this.state.enabled = true
  }
}

class Node extends BasicEntity {
  constructor (id, name, nextNode) {
    super(id, name, new NodeState())
    this.nextNode = nextNode
    if (nextNode) {
      nextNode.subscribe(this)
    }
  }

  setNextNode (nextNode) {
    if (this.nextNode) {
      this.nextNode.unsubscribe(this)
    }
    this.nextNode = nextNode
    if (this.nextNode) {
      nextNode.subscribe(this)
    }
  }

  prestige () {
    this.state.executed = false
  }

  executeAction () {
    this.state.executed = true
  }
}

class UserChoiceNode extends Node {
  constructor (id, name, paths) {
    super(id, name, null)
    this.paths = paths
  }

  selectPath (path) {
    if (path.state.enabled) {
      path.state.selected = true
    }
    for (let path of this.paths) {
      path.state.enabled = false
    }
    this.update()
  }

  update () {
    for (let path of this.paths) {
      if (path.state.selected) {
        this.setNextNode(path.nextNode)
      }
    }
    super.update()
  }

  prestige () {
    this.setNextNode(null)
    super.prestige()
  }
}

class PassiveNode extends Node {
}

class PrestigeNode extends Node {
  executeAction () {
    if (!this.state.executed) {
      this.state.executed = true
      prestiger.prestige()
    }
  }
}

class Event extends Node {
  /*
  An event tells a story in the protagonist's life.
  This story is represented as a tree.
  At the nodes, the future depends on either the protagonist's decision, his stats, his past, or a combination of those.
  Therefore, there should be two kind of nodes: decision nodes which require player input.
  And automatic notes which decide what happens based on the state.

  The most important event will be the option to prestige.
  There will be a message on the protagonist's phone: "You failed. Reply 'groundhog' to try again."
  Possibly the message will contain hints about missing pieces of information to solve the prestige mystery.
  The player will need to reply groundhog at some time. Time will slow down more and more if he does not.

  There will be simple events which only depends on the player's stats.
  For example, he might only be able to save a person's life from fire if his fitness level is high enough.
  */
  constructor (id, name, nextNode) {
    super(id, name, nextNode)
    this.nodeList = []
    this.update()
  }

  update () {
    this.computeNodeList()
    super.update()
  }

  computeNodeList () {
    this.nodeList = []
    if (!this.unlocked()) {
      return
    }
    let currentNode = this.nextNode
    while (currentNode && currentNode.unlocked()) {
      this.nodeList.push(currentNode)
      currentNode.executeAction()
      currentNode = currentNode.nextNode
    }
  }
}

const prestigeNode = new PrestigeNode('event_groundhog_prestige', 'Here we go!', null)

const replyGroundhog = new Path('replyGroundhog', 'Reply "groundhog"', prestigeNode)
const replyGroundhogNode = new UserChoiceNode('groundhoggingRoot', 'A message on your phone: \'We failed. Reply "groundhog" to try again.\'', [replyGroundhog])
const groundhog2 = new PassiveNode('event_groundhog2', 'Every day appears to last longer than the day before.', replyGroundhogNode)
const groundhog1 = new PassiveNode('event_groundhog1', 'Something feels wrong...', groundhog2)
const groundHogging = new Event('groundhogging', 'Groundhog', groundhog1)

createMinimumValueLock(currentDay, groundHogging, 100)

export const events = [groundHogging]



