import { Book } from '../book'
import { Area } from '../area'

class AccountingMadeSimple extends Book {
  constructor () {
    super('acc1', 'Accounting Made Simple', 'Accounting in 100 Pages', 50,
    'https://www.amazon.com/Accounting-Made-Simple-Explained-Pages/dp/0981454224/ref=sr_1_1?ie=UTF8&qid=1485930125&sr=8-1&keywords=accounting')
  }
}

const accountingMadeSimple = new AccountingMadeSimple()

export const accounting = new Area('area_accounting', 'Accounting', [accountingMadeSimple])



