<template>
  <tbody>
  <tr>
        <td>
          <button type="button" :class="['btn', subject.state.completed ? 'btn-success' : (subject.state.onReadingList ? 'btn-info' : 'btn-secondary')]"
            :disabled="!subject.locks.allOpen" :title="subject.locks.lockStrings()"
            v-on:click="addToReadingList(subject)" style="width: 100%">{{ subject.locks.allOpen ? subject.name : '?' }}</button>
        </td>
        <td>
          {{ subject.hours }}
        </td>
    </tr>
    <tr><td><small> {{ subject.description }} </small></td></tr>
    <tr><td> {{ subject.effect }} </td></tr>
    <tr><td style='padding: 1rem'></td></tr>
  </tbody>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'subject-button',
  props: ['subject'],
  methods: mapActions(['addToReadingList']),
  computed: mapGetters(['currency'])
}
</script>



