import { BasicEntity } from '../basicEntity'
import { assert } from '../assert'

class ReadingListState {
}

class ReadingList extends BasicEntity {
  constructor () {
    super('readingList', 'Reading List', new ReadingListState())
    this.books = []
  }

  prestige () {
    this.books = []
  }

  postPrestigeAssert () {
    assert(this.books.length <= 0)
  }

  add (book) {
    if (!this.books.includes(book)) {
      this.books.push(book)
      this.update()
      return this.books.length - 1
    } else {
      return this.books.indexOf(book)
    }
  }

  addInPosition (book, position) {
    while (position >= this.books.length) {
      this.books.push(null)
    }
    this.books[position] = book
  }

  advance (duration) {
    if (this.books.length <= 0) {
      return
    }
    this.books[0].state.timeLeft -= duration / 60
    if (this.books[0].state.timeLeft <= 0) {
      this.books[0].complete()
      this.books.shift()
      for (let book of this.books) {
        book.state.positionOnReadingList -= 1
        book.update()
      }
    }
  }
}

export const readingList = new ReadingList()



