<template>
  <div>
    <div class="row">
      <!--
      <div class="col-md-4">
        <button class='btn m-3' role='button' v-on:click="autoPromote.toggleValue()">Auto-Promote is {{ autoPromote.select('On', 'Off') }}</button>
      </div>
    -->
    </div>
    <div v-for="career in careers">
      <div class="row">
        <h5>{{ career.name }}</h5><br>
      </div>
      <table>
        <tr>
          <th style='width:15rem'>Title</th><th style='width:5rem'>Level</th><th>Experience</th><th style='width:10rem'></th><th style='width:5rem'>Income</th>
        </tr>
        <job-button v-for="job in career.jobs" :job="job" v-if="job.locks.keyholdersUnlocked()"></job-button>
      </table>
    </div>
  </div>
</template>

<script>
import JobButton from 'components/JobButton'
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'job-selector',
  components: {
    JobButton
  },
  computed: mapGetters(['careers', 'autoPromote']),
  methods: mapActions([])
}
</script>



