import { IncomePerWorkHourStat } from '../stats/incomePerWorkHour'
import { BasicEntity } from '../basicEntity.js'
import { currentJobContainer } from './currentJobContainer'
import { XPModule } from '../XPModule'
import { workExperiencePerHourStat } from '../stats/workExperiencePerHour'
import { exponentialProgression } from '../exponentialProgression'
import { assert } from '../assert'

export class Job extends BasicEntity {
  constructor (id, name, basePay = 10, active = false) {
    const state = new JobState()
    state.active = active
    super(id, name, state)
    this.xp = new XPModule(this.id + 'experience', this.name + ' experience', workExperiencePerHourStat)
    this.income = new IncomePerWorkHourStat(this, basePay)
    this.update()
  }

  setBasePay (basePay) {
    this.income.setBase(basePay)
    this.update()
  }

  update () {
    if (this.state.active) {
      currentJobContainer.update()
    }
    super.update()
  }

  activate () {
    this.state.active = true
    currentJobContainer.setCurrentJob(this)
    this.update()
  }

  deactivate () {
    this.state.active = false
    this.update()
  }

  nextLevelAtExp (level) {
    return 1
  }

  prestige () {
  }

  postPrestigeAssert () {
    assert(this.id === 'bm1' || !this.state.active)
    assert(this.id !== 'bm1' || this.state.active)
  }
}

export class JobState {
  constructor () {
    this.active = false
  }
}

export function configurePayProgression (jobs, firstJobPay, finalJobPay, succFactor) {
  let payProgression = exponentialProgression(jobs.length, succFactor)
  payProgression = payProgression.map(p => p * finalJobPay / payProgression.slice(-1)[0])
  payProgression = payProgression.map(p => p + (firstJobPay - payProgression[0]))
  payProgression[payProgression.length - 1] = finalJobPay
  for (let i = 0; i < jobs.length; i++) {
    jobs[i].income.setBase(payProgression[i])
  }
}



