import { BasicEntity } from '../basicEntity'
import { readingList } from './readingList'
import { assert } from '../assert'

class BookState {
  constructor (timeLeft) {
    this.completed = false
    this.onReadingList = false
    this.timeLeft = timeLeft
    this.originalTimeLeft = timeLeft
    this.readingListPosition = 0
    this.timesCompleted = 0
  }
}

export class Book extends BasicEntity {
  constructor (id, name, description, hours, buyLink = '') {
    super(id, name, new BookState(hours))
    this.description = description
    this.hours = hours
    this.buyLink = buyLink
    this.effect = ''
  }

  prestige () {
    this.state.completed = false
    this.state.onReadingList = false
    this.state.timeLeft = this.state.originalTimeLeft / (1 + this.state.timesCompleted)
    this.state.readingListPosition = 0
  }

  postPrestigeAssert () {
    assert(!this.completed)
    assert(!this.onReadingList)
    assert(this.state.timeLeft)
    assert(this.state.readingListPosition === 0)
  }

  onLoad () {
    if (this.state.onReadingList) {
      readingList.addInPosition(this, this.state.readingListPosition)
    }
    super.onLoad()
  }

  addToReadingList () {
    this.state.onReadingList = true
    this.state.readingListPosition = readingList.add(this)
    this.update()
  }

  complete () {
    this.state.onReadingList = false
    this.state.completed = true
    this.state.timesCompleted += 1
    console.log('completed ', this.id, this.name)
    this.update()
  }
}



