import { BasicEntity } from '../basicEntity'
import { Stat } from '../stats/stat'
import { NumberStateEntity } from '../gameState/stateEntity'
import { currentJobContainer } from '../jobs/currentJobContainer'
import { currentResearchContainer } from '../library/currentResearchContainer'
import { assert } from '../assert'

class DurationProxy extends NumberStateEntity {
}

class Duration extends Stat {
  constructor (id, name, duration) {
    super(id, name, duration)
    this.updateDurationString()
  }

  update () {
    super.update()
    this.updateDurationString()
  }

  updateDurationString () {
    const hours = Math.floor(this.effective / 60)
    const minutes = this.effective - hours * 60
    const minutesString = ('00' + minutes).substr(-2)
    this.durationString = hours + ':' + minutesString
  }
}

class ActivityState {
}

class Activity extends BasicEntity {
  constructor (id, name, ingName, duration, durationByUser, color) {
    super(id, name, new ActivityState())
    this.originalDuration = duration
    this.ingName = ingName
    this.durationByUser = durationByUser
    this.color = color
    this.isCurrent = false
    this.duration = new Duration(this.id + 'duration', this.name + ' duration', duration)
    this.update()
  }

  prestige () {
    this.duration.base = this.originalDuration
    this.duration.update()
    if (this.durationProxy) {
      this.durationProxy.setValue(this.duration.effective)
    }
  }

  postPrestigeAssert () {
    assert(this.duration.base === this.originalDuration)
  }

  update () {
    if (this.hasOwnProperty('durationProxy')) {
      this.duration.base = this.durationProxy.getValue()
      this.duration.update()
      assert(this.duration.base === this.durationProxy.getValue())
      assert(this.duration.base === this.duration.effective)
    }
    super.update()
  }

  getDuration () {
    return this.duration.effective
  }

  do (state) {
    return
  }
}

class Sleep extends Activity {
  constructor () {
    super('activitySleep', 'Sleep', 'sleeping', 8 * 60, false, '#007D43')
  }

  do (state) {
    return
  }
}

export const sleep = new Sleep()

class Work extends Activity {
  constructor () {
    super('activityWork', 'Work', 'working', 8 * 60, true, '#F6768E')
    this.durationProxy = new DurationProxy('workDurationProxy', 'Work Duration Proxy')
    this.durationProxy.setValue(this.duration.effective, true)
    this.durationProxy.subscribe(this)
    this.update()
  }

  do (state) {
    currentJobContainer.job.xp.gainExperience(this.getDuration())
  }
}

export const work = new Work()

class Study extends Activity {
  constructor () {
    super('activityStudy', 'Read', 'reading', 0 * 60, true, 'FF7A5C')
    this.durationProxy = new DurationProxy('studyDurationProxy', 'Study Duration Proxy')
    this.durationProxy.setValue(this.duration.effective, true)
    this.durationProxy.dependants.push(this)
    this.update()
  }

  do (state) {
    if (state.readingList.books.length > 0) {
      state.readingList.advance(this.getDuration())
    }
  }
}

export const study = new Study()

class Research extends Activity {
  constructor () {
    super('activityStudy', 'Research', 'researching', 0 * 60, true, 'FF7A5C')
    this.durationProxy = new DurationProxy('researchDurationProxy', 'Research Duration Proxy')
    this.durationProxy.setValue(this.duration.effective, true)
    this.durationProxy.dependants.push(this)
    this.update()
  }

  do (state) {
    if (currentResearchContainer.area) {
      currentResearchContainer.area.xp.gainExperience(this.getDuration())
    }
  }
}

export const research = new Research()

class Eat extends Activity {
  constructor () {
    super('activityEat', 'Eat', 'eating', 3 * 60, false, '#00538A')
  }

  do (state) {
    return
  }
}

export const eat = new Eat()

class Chores extends Activity {
  constructor () {
    super('activityChores', 'Chores', 'Cleaning', 2 * 60, false, '#000000')
  }
}

export const chores = new Chores()

class Slacking extends Activity {
  constructor () {
    super('activitySlacking', 'Slacking', 'slacking', 3 * 60, false, '#000000')
  }
}

export const slacking = new Slacking()

class FreeTime extends Activity {
  constructor () {
    super('activityFreeTime', 'Free Time', 'slacking', 0 * 60, false, '#53377A')
    this.durationProxy = new DurationProxy('freeTimeDurationProxy', 'Free Time Duration Proxy')
    this.durationProxy.setValue(this.duration.effective)
    this.durationProxy.dependants.push(this)
    this.update()
  }
}

export const freeTime = new FreeTime()
export const activities = [sleep, work, study, research, eat, chores, slacking, freeTime]



