import {
  BasicEntity
} from '../basicEntity'
import { XPModule } from '../XPModule'
import { researchExperiencePerHourStat } from '../stats/researchExperiencePerHour'
import { createCompletedLock } from '../locks'
import { currentResearchContainer } from './currentResearchContainer'
import { assert } from '../assert'

class AreaState {
  constructor () {
    this.active = false
  }
}

export class Area extends BasicEntity {
  constructor (id, name, subjects) {
    super(id, name, new AreaState())
    this.name = name
    this.subjects = subjects
    for (let subject of subjects) {
      createCompletedLock(subject, this)
    }
    this.xp = new XPModule(this.id + 'experience', this.name + ' experience', researchExperiencePerHourStat)
    this.effect = ''
  }

  activate () {
    this.state.active = true
    currentResearchContainer.setCurrentResearch(this)
    this.update()
  }

  deactivate () {
    this.state.active = false
    this.update()
  }

  prestige () {
    this.state.active = false
  }

  postPrestigeAssert () {
    assert(!this.state.active)
  }
}



