class Prestiger {
  constructor () {
    this.registeredObjects = []
  }

  register (basicEntity) {
    this.registeredObjects.push(basicEntity)
  }

  prestige () {
    for (let object of this.registeredObjects) {
      object.prestige()
    }
    for (let object of this.registeredObjects) {
      object.update()
    }
    for (let object of this.registeredObjects) {
      object.postPrestigeAssert()
    }
  }
}

export const prestiger = new Prestiger()



