import { Modifier } from './modifier'
import { energyStat } from '../stats/energy'

class StandardEnergyModifier extends Modifier {
  constructor () {
    super('seMod', 'Energy', 0)
    energyStat.dependants.push(this)
    this.update()
  }

  update () {
    this.factor = energyStat.effective
    super.update()
  }
}

export const standardEnergyModifier = new StandardEnergyModifier()



