import { leadership101 } from '../library/business/leadership'
import { Job, configurePayProgression } from './job'
import { configureXpProgression } from '../XPModule'
import { Career } from './career'
import { createLevelLockChain, createCompletedLock } from '../locks.js'

class BurgerFlipper extends Job {
  constructor () {
    super('bm1', 'Burger Flipper', 10, true)
  }
}

export const burgerFlipper = new BurgerFlipper()

class ChiefFlipper extends Job {
  constructor () {
    super('bm2', 'Chief Flipper')
  }
}

const chiefFlipper = new ChiefFlipper()

class KitchenManager extends Job {
  constructor () {
    super('bm3', 'Kitchen Manager')
  }
}

const kitchenManager = new KitchenManager()

class ShiftManager extends Job {
  constructor () {
    super('bm4', 'Shift Manager')
  }
}

const shiftManager = new ShiftManager()

class AssistantManager extends Job {
  constructor () {
    super('bm5', 'Assistant Manager')
  }
}

const assistantManager = new AssistantManager()

class RestaurantManager extends Job {
  constructor () {
    super('bm6', 'Restaurant Manager')
  }
}

const restaurantManager = new RestaurantManager()

class RegionalManager extends Job {
  constructor () {
    super('bm7', 'Regional Manager')
  }
}

const regionalManager = new RegionalManager()

class ChiefStrategist extends Job {
  constructor () {
    super('bm8', 'RegionalManager')
  }
}

const chiefStrategist = new ChiefStrategist()

class CEO extends Job {
  constructor () {
    super('bm9', 'CEO')
  }
}

const ceo = new CEO()

const jobChain = [burgerFlipper, chiefFlipper, kitchenManager, shiftManager, assistantManager, restaurantManager, regionalManager, chiefStrategist, ceo]
configureXpProgression(jobChain, 200, 2.2, 8)
configurePayProgression(jobChain, 5, 500, 2.0)

createLevelLockChain(jobChain, 10)

createCompletedLock(leadership101, shiftManager)

export const bunMasters = new Career('bunMasters', 'BunMasters', jobChain)



