<template>
<div class="text-left card">
      {{ currency.money.getValue().toFixed(0) }}$<br>
      {{ stats.dailyNetIncome.effective.toFixed(2)}}$ per day<br>
      <ul>
        <li>Daily Income: {{ stats.dailyIncome.effective.toFixed(2)}}$</li>
        <li>Daily Expenses: {{ stats.dailyExpenses.effective.toFixed(2) }}$</li>
      </ul>
      Energy: {{ stats.energy.effective.toFixed(2) }}<br>
      Work Experience/h: {{ stats.workExperiencePerHour.effective.toFixed(2) }}/h
</div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'currency-display',
  computed: mapGetters(['activities', 'currency', 'stats']),
  methods: mapActions(['changeActivityTime'])
}
</script>



