import { gungHo } from '../library/business/leadership'
import { Job, configurePayProgression } from './job'
import { Career } from './career'
import { createLevelLockChain, createCompletedLock } from '../locks'
import { configureXpProgression } from '../XPModule'

class UnpaidIntern extends Job {
  constructor () {
    super('yugle1', 'Unpaid Intern')
  }
}

const unpaidIntern = new UnpaidIntern()

class JuniorDeveloper extends Job {
  constructor () {
    super('yugle2', 'Junior Developer')
  }
}

const juniorDeveloper = new JuniorDeveloper()

class SoftwareEngineer extends Job {
  constructor () {
    super('yugle3', 'Software Engineer')
  }
}

const softwareEngineer = new SoftwareEngineer()

class ProjectLead extends Job {
  constructor () {
    super('yugle4', 'Project Lead')
  }
}

const projectLead = new ProjectLead()

class TeamLead extends Job {
  constructor () {
    super('yugle5', 'Team Lead')
  }
}

const teamLead = new TeamLead()

class SeniorArchitect extends Job {
  constructor () {
    super('yugle6', 'Senior Architect')
  }
}

const seniorArchitect = new SeniorArchitect()

class DivisionLead extends Job {
  constructor () {
    super('yugle7', 'Division Lead')
  }
}

const divisionLead = new DivisionLead()

class ProductManager extends Job {
  constructor () {
    super('yugle8', 'ProductManager')
  }
}

const productManager = new ProductManager()

class CTO extends Job {
  constructor () {
    super('yugle9', 'CTO')
  }
}

const cto = new CTO()

class CEO extends Job {
  constructor () {
    super('yugle10', 'CEO')
  }
}

const ceo = new CEO()

const jobChain = [unpaidIntern, juniorDeveloper, softwareEngineer, projectLead, teamLead, seniorArchitect, divisionLead, productManager, cto, ceo]
configureXpProgression(jobChain, 400, 2, 8)
configurePayProgression(jobChain, 15, 1000, 2.0)
unpaidIntern.setBasePay(0)

createLevelLockChain(jobChain, 10)

createCompletedLock(gungHo, teamLead)

export const yugle = new Career('yugle', 'Yugle', jobChain)



