<template>
  <div>
    <div class="row">
      <!--
      <div class="col-md-4">
        <button class='btn m-3' role='button' v-on:click="autoPromote.toggleValue()">Auto-Promote is {{ autoPromote.select('On', 'Off') }}</button>
      </div>
    -->
    </div>
    <div v-for="event in events" v-if="event.nodeList.length > 0">
      <div class="row">
        <h5>{{ event.name }}</h5><br>
      </div>
      <div v-for="node in event.nodeList">
        {{ node.name }}<br>
        <div v-if="node.paths">
          <div v-for="path in node.paths">
            <button class='btn' role='button' :disabled="!path.state.enabled" @click="node.selectPath(path)">{{ path.name }}</button>
          </div>
        </div>
      <div>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'events-display',
  components: {
  },
  computed: mapGetters(['events']),
  methods: mapActions([])
}
</script>



