import { BasicEntity } from './basicEntity'
import { assert } from './assert'

class XPState {
  constructor () {
    this.xp = 0
    this.level = 1
    this.levelsReached = []
  }
}

export const linearXpIncrease = function (factor) {
  const nextLevelAtXp = function (level) {
    return level * factor
  }
  return nextLevelAtXp
}

export class XPModule extends BasicEntity {
  constructor (id, name, xpPerHourStat, nextLevelAtXp = linearXpIncrease(10)) {
    super(id, name, new XPState())
    this.xpPerHourStat = xpPerHourStat
    this.nextLevelAtXp = nextLevelAtXp
    this.update()
  }

  timesReachedLevel () {
    return this.state.levelsReached.map(x => x > this.state.level ? 1 : 0).reduce((x, y) => x + y, 0)
  }

  computePrestigeFactor () {
    return 1 + this.timesReachedLevel()
  }

  gainExperience (duration) {
    let prestigeFactor = this.computePrestigeFactor()
    let baseXpGain = this.xpPerHourStat.effective * duration / 60
    let effectiveXp = baseXpGain * prestigeFactor
    let levelUp = false
    while (baseXpGain > 0) {
      let xpForNext = this.nextLevelAtXp(this.state.level)
      if (effectiveXp + this.state.xp >= xpForNext) {
        effectiveXp -= (xpForNext - this.state.xp)
        baseXpGain -= effectiveXp / prestigeFactor
        this.state.level += 1
        this.state.xp = 0
        levelUp = true
        prestigeFactor = this.computePrestigeFactor()
        effectiveXp = baseXpGain * prestigeFactor
      } else {
        this.state.xp += effectiveXp
        baseXpGain = 0
      }
    }
    if (levelUp) {
      this.update()
    }
  }

  prestige () {
    this.state.levelsReached.push(this.state.level)
    this.state.xp = 0
    this.state.level = 1
  }

  postPrestigeAssert () {
    assert(this.state.xp === 0)
    assert(this.state.level === 1)
    assert(this.state.levelsReached.length > 0)
  }
}

export const configureXpProgression = function (xpEntities, totalYears, succFactor, dailyExp) {
  const totalXp = Math.round(totalYears * 365 * dailyExp)
  let xpProgression = xpEntities.map(x => 1.0)
  for (let i = 1; i < xpProgression.length; i++) {
    xpProgression[i] = xpProgression[i - 1] * succFactor
  }
  const xpSum = xpProgression.reduce((a, b) => a + b)
  xpProgression = xpProgression.map(x => x * totalXp / xpSum)
  const xpBaselines = xpProgression.map(x => x / 55) // linearly increasing XP up to level 10
  for (let i = 0; i < xpProgression.length; i++) {
    xpEntities[i].xp.nextLevelAtXp = linearXpIncrease(Math.ceil(xpBaselines[i]))
  }
}



