import Vue from 'vue'
import store from './vuex/store'
import App from './App'
window.jQuery = window.$ = require('jquery')
window.Tether = require('tether')
require('bootstrap')
require('bootstrap/dist/css/bootstrap.css')
require('./global.css')
require('./ga.js')

/* eslint-disable no-new */
new Vue({
  store,
  el: '#app',
  template: '<App/>',
  components: { App }
})



