<template>
<div id="app">
  <div class="container">
    <div class="row">
      <progress-modal></progress-modal>
      <div class="col-md-4">
        <clock-display></clock-display>
        <div class="row">
          <schedule-controller></schedule-controller>
        </div>
        <div class="row">
          <currency-display></currency-display>
        </div>
        <div class="row">
          <reading-list></reading-list>
        </div>
      </div>
      <div class="col-md-8">
        <ul class="nav nav-tabs">
          <li class="nav-item">
            <a class="nav-link active" data-toggle="tab" href="#job">Job</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" data-toggle="tab" href="#reading">Reading</a>
          </li>
          <li :class="['nav-item', research.unlocked() ? 'item-unlocked' : 'item-locked']">
            <a class="nav-link" data-toggle="tab" href="#research">Research</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" data-toggle="tab" href="#events">Events</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" data-toggle="tab" href="#achievements">Achievements</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" data-toggle="tab" href="#settings">Settings</a>
          </li>
        </ul>

        <div class="tab-content">
          <div class="tab-pane active" id="job" role="tabpanel">
            <job-selector></job-selector>
          </div>
          <div class="tab-pane" id="reading" role="tabpanel">
            <div class="row">
                <study-fields></study-fields>
            </div>
          </div>
          <div class="tab-pane" id="research" role="tabpanel">
            <div class="row">
                <area-selector></area-selector>
            </div>
          </div>
          <div class="tab-pane" id="events" role="tabpanel">
            <div class="row">
                <events-display></events-display>
            </div>
          </div>
          <div class="tab-pane" id="achievements" role="tabpanel">
            achievements
          </div>
          <div class="tab-pane" id="settings" role="tabpanel">
            <settings-display></settings-display>
          </div>
        </div>
      </div>
    </div>
    <!-- /container -->
  </div>
</div>
</template>

<script>
import ClockDisplay from './components/ClockDisplay'
import ScheduleController from './components/ScheduleController'
import CurrencyDisplay from './components/CurrencyDisplay'
import JobSelector from './components/JobSelector'
import StudyFields from './components/study/StudyFields'
import ProgressModal from './components/ProgressModal'
import SettingsDisplay from './components/SettingsDisplay'
import ReadingList from './components/study/ReadingList'
import AreaSelector from './components/research/AreaSelector'
import EventsDisplay from './components/events/EventsDisplay'
import {
  mapGetters,
  mapActions
} from 'vuex'

export default {
  name: 'app',
  components: {
    ClockDisplay,
    ScheduleController,
    CurrencyDisplay,
    JobSelector,
    StudyFields,
    ProgressModal,
    SettingsDisplay,
    ReadingList,
    AreaSelector,
    EventsDisplay
  },
  computed: mapGetters(['timeConfig', 'time', 'research']),
  methods: {
    mainLoop: function () {
      let dateObject = new Date()
      let currentTime = dateObject.getTime()
      let timeDiff = currentTime - this.time.lastTime
      let tickLength = this.timeConfig.tickLength
      let progressModal = window.$('#progress-modal')
      let progressModalTransitioning = (progressModal.data('bs.modal') || {_isTransitioning: false})._isTransitioning
      if (!progressModalTransitioning) {
        let progressModalOpen = (progressModal.data('bs.modal') || {_isShown: false})._isShown
        if (timeDiff > 5000 && !progressModalOpen) {
          progressModal.modal('show')
        }
        if (timeDiff < 1000 && progressModalOpen) {
          progressModal.modal('hide')
        }
      }
      let ticksLeft = timeDiff / tickLength
      let tickBatch = 1
      if (ticksLeft > 50) {
        tickBatch = 10
      }
      if (tickLength < timeDiff) {
        this.tick(tickBatch)
        timeDiff -= tickLength * tickBatch
      }
      if (tickLength < timeDiff) {
        setTimeout(this.mainLoop, 2)
      } else {
        setTimeout(this.mainLoop, tickLength / 3)
      }
    },
    ...mapActions(['tick', 'initializeTiming'])
  },
  created: function () {
    this.initializeTiming()
    this.mainLoop()
  }
}

</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



