import { BasicEntity } from '../basicEntity'
import { assert } from '../assert'

export class StateEntity extends BasicEntity {
  constructor (id, name, StateClass, initialValue, resetOnPrestige) {
    super(id, name, new StateClass(initialValue))
    this.initialValue = initialValue
    this.resetOnPrestige = resetOnPrestige
  }

  prestige () {
    if (this.resetOnPrestige) {
      this.state.value = this.initialValue
    }
  }

  postPrestigeAssert () {
    assert(!this.resetOnPrestige || this.state.value === this.initialValue)
  }

  setValue (value, updateInitialValue = false) {
    this.state.value = value
    if (updateInitialValue) {
      this.initialValue = value
    }
    super.update()
  }

  getValue () {
    return this.state.value
  }
}

class BooleanState {
  constructor (value) {
    this.value = value
  }
}

export class BooleanStateEntity extends StateEntity {
  constructor (id, name, value = false, resetOnPrestige = true) {
    super(id, name, BooleanState, value, resetOnPrestige)
  }

  toggleValue () {
    this.state.value = !this.state.value
    super.update()
  }

  select (ifTrue, ifFalse) {
    return this.state.value ? ifTrue : ifFalse
  }
}

class NumberState {
  constructor (value) {
    this.value = value
  }
}

export class NumberStateEntity extends StateEntity {
  constructor (id, name, value = 0, resetOnPrestige = true) {
    super(id, name, NumberState, value, resetOnPrestige)
  }

  incrementValue () {
    this.state.value += 1
    super.update()
  }

  decrementValue () {
    this.state.value -= 1
    super.update()
  }

  add (delta) {
    this.state.value += delta
    super.update()
  }
}



