<template>
<div>
  <ul class="nav nav-tabs">
    <li class="nav-item" v-for="field in fields">
      <a class="nav-link" data-toggle="tab" :href="'#' + field.id">{{ field.name }}</a>
    </li>
  </ul>

  <div class="tab-content">
    <div class="tab-pane" :id="field.id" role="tabpanel" v-for="field in fields">
      <field-display :field="field"></field-display>
    </div>
  </div>
</div>
</template>

<script>
import FieldDisplay from './FieldDisplay'
import {
  mapGetters
} from 'vuex'

export default {
  name: 'study-fields',
  components: {
    FieldDisplay
  },
  computed: mapGetters(['fields'])
}
</script>



