import { Stat } from './stat'
import { standardEnergyModifier } from '../modifiers/standardEnergy'

class ResearchExperiencePerHourStat extends Stat {
  constructor () {
    super('repH', 'Research/h', 1.0)
    this.addModifier(standardEnergyModifier)
    this.update()
  }
}

export const researchExperiencePerHourStat = new ResearchExperiencePerHourStat()



