<template>
<div>
  <button class="btn" role="button" v-on:click="hardReset()">Hard Reset</button>
</div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'settings-display',
  computed: mapGetters([]),
  methods: mapActions(['hardReset'])
}
</script>



