import { BasicEntity } from '../basicEntity'

class ModifierState {
}

export class Modifier extends BasicEntity {
  constructor (id, name, priority, factor = 1) {
    super(id, name, new ModifierState())
    this.factor = factor
    this.priority = priority
  }

  prestige () {
  }

  apply (base) {
    return base * this.factor
  }
}

export class LevelModifier extends Modifier {
  constructor (id, name, priority, levelEntity, factorPerLevel) {
    super(id, name, priority)
    this.levelEntity = levelEntity
    levelEntity.subscribe(this)
    this.factorPerLevel = factorPerLevel
    this.update()
  }

  apply (base) {
    return base * Math.pow(this.factorPerLevel, this.levelEntity.state.level - 1)
  }
}



