import { Stat } from './stat'
import { standardEnergyModifier } from '../modifiers/standardEnergy'

class WorkExperiencePerHourStat extends Stat {
  constructor () {
    super('wepH', 'Work experience/h', 1.0)
    this.addModifier(standardEnergyModifier)
    this.update()
  }
}

export const workExperiencePerHourStat = new WorkExperiencePerHourStat()



