<template>
  <div>
    <div class="row">
      <!--
      <div class="col-md-4">
        <button class='btn m-3' role='button' v-on:click="autoPromote.toggleValue()">Auto-Promote is {{ autoPromote.select('On', 'Off') }}</button>
      </div>
    -->
    </div>
    <div v-for="field in fields">
      <div class="row">
        <h5>{{ field.name }}</h5><br>
      </div>
      <table>
        <tr>
          <th style='width:15rem'></th><th style='width:5rem'>Level</th><th>Experience</th><th style='width:10rem'></th><th style='width:5rem'>Effect</th>
        </tr>
        <area-button v-for="area in field.areas" :area="area" v-if="area.locks.allOpen"></area-button>
      </table>
    </div>
  </div>
</template>

<script>
import AreaButton from 'components/research/AreaButton'
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'area-selector',
  components: {
    AreaButton
  },
  computed: mapGetters(['fields']),
  methods: mapActions([])
}
</script>



