<template>
  <tr>
        <td>
          <button type="button" :class="['btn', area.state.active ? 'btn-success' : 'btn-secondary']"
            :disabled="!area.locks.allOpen" :title="area.locks.lockStrings()"
            v-on:click="setActiveResearch(area)" style="width: 100%">{{ area.locks.allOpen ? area.name : '?' }}</button>
        </td>
        <td>
          {{ area.xp.state.level }}
        </td>
        <td>
          <progress class="progress" :value="area.xp.state.xp" :max="area.xp.nextLevelAtXp(area.xp.state.level)"></progress>
        </td>
        <td>
          {{ area.xp.state.xp.toFixed(0) }}xp / {{ area.xp.nextLevelAtXp(area.xp.state.level) }}xp<br>
        </td>
        <td>
        </td>
    </tr>
</div>

</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'research-button',
  props: [
    'area'
  ],
  computed: mapGetters([]),
  methods: mapActions(['setActiveResearch'])
}
</script>



